:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  min-height: 100vh;
  background-color: #f4f4fd;
  flex-direction: column;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

img {
  max-width: 100%;
  display: block;
}

.search-form {
  max-width: 1140px;
  justify-content: center;
  margin: 20px auto;
  display: flex;
}

.search-button {
  float: left;
  width: 20%;
  color: #fff;
  cursor: pointer;
  background: #2196f3;
  border: 1px solid gray;
  border-left: none;
  padding: 10px;
  font-size: 17px;
}

.search-button:hover {
  background: #0b7dda;
}

.search-input {
  float: left;
  width: 80%;
  background: #f1f1f1;
  border: 1px solid gray;
  padding: 10px;
  font-size: 17px;
}

.search-input:after {
  content: "";
  clear: both;
  display: table;
}

.gallery {
  max-width: 1140px;
  flex-wrap: wrap;
  justify-content: space-between;
  row-gap: 20px;
  margin: 0 auto;
  display: flex;
}

.photo-card {
  width: 32%;
  flex-direction: column;
  display: flex;
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 300px;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  text-align: center;
  background-color: #fff;
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
  font-size: 15px;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background: #2196f3;
  border: 1px solid gray;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 20px;
  font-size: 17px;
  display: none;
}

